<template>
  <div class="bottom-menu">
    <cart-button class="select-all" :value-checked="isSelectAll" @click.native="checkedClick"></cart-button>
    <span>全选</span>
    <span class="total-price">合计: ¥{{toAllPrice}}</span>
    <span class="buy-product" @click="insOrders">去购买：({{checkedLength}})</span>
  </div>
</template>

<script>
  import CartButton from './CartButton'
  import {insOrder} from 'network/order'
  import {delShoppingById} from 'network/cart'
  export default {
    name: 'CartBottomBar',
    components: {
      CartButton
    },
    computed: {
      //计算当前选中的总价格
      toAllPrice() {
        return this.$store.state.cartList.filter(item => {
          return item.sgstatus
        }).reduce((preValue, item) => {
          return preValue + item.sgprice * item.sgcount
        }, 0).toFixed(2)
      },
      //计算当前已选中的数量
      checkedLength() {
        return this.$store.state.cartList.filter(item => item.sgstatus).length
      },
      //计算被选中的状态
      isSelectAll() {
        //如果购物车中的长度为0 返回false
        if(this.$store.state.cartList.length == 0) return false
        //查找购物车中  在数组中找状态有没有为未选中的 有就返回false 反之 返回true
        return !this.$store.state.cartList.find(item => !item.sgstatus)
      }
    },
    methods: {
      //点击全选按钮
      checkedClick() {
        if(this.isSelectAll) {//全部选中时    全部变为不选中
          this.$store.state.cartList.forEach(item => item.sgstatus = false)
        } else {//部分选中或全部不选中   变为全部选中
          this.$store.state.cartList.forEach(item => item.sgstatus = true)
        }
      },
      insOrders() {
        //添加订单  判断订单数量
        //获取选中的商品
        if(this.checkedLength == 0){
          this.$toast.show("请选择商品")
        } else {
          //遍历处于选中状态的商品数组
          let cartL = this.$store.state.cartList;
          let newCart = [];
          let lastCart = [];
          for(let item of cartL) {
            if(item.sgstatus == true) {
              newCart.push(item);
            } else {
              lastCart.push(item)
            }
          }
          for(let item of newCart) {
            insOrder(item.uid,item.gid,item.sgtitle,item.sgimg,
                  item.sgsize,item.sgprice * item.sgcount,item.sgcount).then(res => {
              //将购买后的商品移除购物车
              this.$toast.show(res)
              if(res == "购买成功") {
              console.log(item.sid)
              console.log(item)
                this.$store.state.cartList = lastCart;
                //发送请求 根据sid删除购物车中的商品
                delShoppingById(item.sid).then(res => {

                })
              }
            })
          }
        }      
      }
    }
  }
</script>

<style scoped>
  .bottom-menu {
    width: 100%;
    height: 44px;
    background-color: #eee;
    position: fixed;
    bottom: 50px;
    left: 0;
    box-shadow: 0 -2px 3px rgba(0, 0, 0, .2);
    font-size: 14px;
    color: #888;
    line-height: 44px;
    padding-left: 35px;
    box-sizing: border-box;
  }

  .bottom-menu .select-all {
    position: absolute;
    line-height: 0;
    left: 12px;
    top: 13px;
  }

  .bottom-menu .total-price {
    margin-left: 15px;
    font-size: 16px;
    color: #666;
  }

  .bottom-menu .buy-product {
    background-color: orangered;
    color: #fff;
    width: 100px;
    height: 44px;
    text-align: center;
    line-height: 44px;
    float: right;
  }
</style>