<template>
  <div id="shop-item">
    <div class="item-selector">
      <cart-botton :value-checked="product.sgstatus" @click.native="checkedItem"></cart-botton>
    </div>
    <div class="item-img" @click="toDetail">
      <img :src="product.sgimg" alt="商品图片">
    </div>
    <div class="item-info">
      <div class="item-title">{{product.sgtitle}}</div>
      <div class="item-desc">商品描述: {{product.sgsize}}</div>
      <div class="info-bottom">
        <div class="item-price">¥{{product.sgprice}}</div>
        <div class="item-count">
          <div class="count-del" @click="countDel">-</div>
          <div>{{product.sgcount}}</div>
          <div class="count-add" @click="countAdd">+</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CartBotton from './CartButton'
import {updShoppingCount,updShoppinfStatus} from 'network/cart'
  export default {
    name: 'CartListItem',
    components: {
      CartBotton
    },
    props: {
      product: {
        type: Object,
        default() {
          return null
        }
      }
    },
    methods: {
      toDetail() {
        this.$router.push('/detail/' + this.product.gid)
      },
      checkedItem() {
        //商品选中的切换
        this.product.sgstatus = !this.product.sgstatus
        //发送请求  根据商品的id切换商品状态
        updShoppinfStatus(this.product.sid,this.product.sgstatus).then(res => {
          
        })
      },
      //购物车商品数量的加减
    countDel() {
      if(this.product.sgcount == 1) {

      }else {
         this.product.sgcount --;
        //发送请求
        updShoppingCount(this.product.sid,this.product.sgcount).then(res => {
         
        })
      }
    },
    countAdd() {
      this.product.sgcount ++;
      //发送请求
       updShoppingCount(this.product.sid,this.product.sgcount).then(res => {
         
        })
      
      
    }      

    }
  }
</script>

<style scoped>
  #shop-item {
    width: 100%;
    display: flex;
    font-size: 0;
    padding: 5px;
    border-bottom: 1px solid #ccc;
  }

  .item-selector {
    width: 14%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .item-title, .item-desc {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .item-img {
    padding: 5px;
    /*border: 1px solid #ccc;*/
  }

  .item-img img {
    width: 80px;
    height: 100px;
    display: block;
    border-radius: 5px;
  }

  .item-info {
    font-size: 17px;
    color: #333;
    padding: 5px 10px;
    position: relative;
    overflow: hidden;
    width: 50%;
  }

  .item-info .item-desc {
    font-size: 14px;
    color: #666;
    margin-top: 15px;
  }

  .info-bottom {
    margin-top: 10px;
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
  }

  .info-bottom .item-price {
    color: orangered;
    float: left;
  }
  .info-bottom .item-count{
    float:right;
    display: flex;
  }
  .item-count > div {
    flex: 1;
    text-align: center;
    font-size: 16px;
    line-height: 22px;
  }
  .count-add, .count-del{
    border: 1px solid black;
    margin-left: 5px;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    border-radius: 5px;
    line-height: 18px;
  }
</style>