<template>
  <div class="cart">
    <!--导航栏-->
    <nav-bar class="cnav-bar"> 
      <div slot="center" >购物车({{cartLength}})</div>
    </nav-bar>
    <!--商品展示-->
    <scroll class="cart-scroll">
      <cart-list :goodsSelect="getGoodsSelect"></cart-list>
    </scroll>
    <!--商品底部汇总-->
    <cart-bottom-bar></cart-bottom-bar>
  </div>
</template>

<script>
  import NavBar from  'common/navbar/NavBar'
    import Scroll from 'common/scroll/Scroll'
    import CartList from './childcomps/CartList'
    import CartBottomBar from './childcomps/CartBottomBar'
  export default {
    name: 'Cart',
    components: {
      NavBar,
      Scroll,
      CartList,
      CartBottomBar
    },
    computed: {
      //购物车的数量
      cartLength() {
        return this.$store.state.cartList.length
      },
      //获取购物车商品的数据
      getGoodsSelect() {
        return this.$store.state.cartList
      }
    }
  }
</script>

<style scoped>
.cart{
  height: 100vh;
}
  .cnav-bar{
    background-color: #ff8198;
    color: white;
  }
  .cart-scroll{
    height: calc(100% - 93px - 44px);
    overflow: hidden;
  }
</style>